  
  $font-1: "font-1", Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  $font-2: "font-2", serif;
  
  $color-0: #009be5;
  $color-1: #fafeff;
  $color-2: #00c1ec;
  $color-3: #e7f7fe;
  $color-4: rgb(92, 90, 90);
  $black: rgb(40, 40, 40);
  $red: rgb(253, 87, 87);
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
    outline: none;
    border: none;
    text-decoration: none;
  
    &::selection {
      color: $color-1;
      background: $color-2;
    }
  }
  html { 
    scroll-behavior: unset;
  }
  a,
  a:visited {
    color: $color-4;
  }
  body {
    font-family: $font-1;
    height: 100vh;
    background-color: $color-1;
    color: $black;
    overflow-x: hidden;
  
  }
  @media screen and (max-width: 430px) {
    body {
      position: relative;
      max-width: 100vw;
      overflow-y: initial;
      overflow-x: hidden;
    }
  }
  main {
    position: relative;
  }
  .button {
    background: $color-3;
    height: 55px;
    width: 126px;
    border-radius: 50px;
    margin-top: 20px;
    font-size: 1.2rem;
    cursor: pointer;
    transition: 0.2s;
  
    &:hover {
      letter-spacing: 1px;
      color: $color-2;
    }
  }
  
  .elt {
    margin: 0;
    width: 100%;
  }
  
   /* MOUSE */
   .cursor {
    width: 62px;
    height: 62px;
    border: 1px solid teal;
    border-radius: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    pointer-events: none;
    transition: 500ms ease-out;
    z-index: 2;
    backdrop-filter: brightness(115%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    -webkit-transition: 500ms ease-out;
    -moz-transition: 500ms ease-out;
    -ms-transition: 500ms ease-out;
    -o-transition: 500ms ease-out;
}
  .cursor.hovered {
    width: 30px;
    height: 30px;
  }

  .wrapper {
    display: flex;
    flex-flow: row wrap;
    text-align: center;
    position: relative;
  
  }
  .wrapper>* {
    padding: 10px;
    margin: 10px;
    flex: 1 100%;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
  }
  
.home {
  height: 100vh;
  width: 100%;
  position: absolute;

  &:after {
    content: '';
    height: 600px;
    width: 600px;
    background: $color-3;
    border-radius: 50%;
    position: absolute;
    bottom: 20px;
    right: 5px;
    z-index: -2;
    animation: homeAnim 26s infinite alternate;

    @media screen and (max-width: 650px){
      height: 400px;
      width: 400px;
      // bottom: -270px;
    }

    @keyframes homeAnim {
      50% {
        transform: scale(0.8);
      }
      100% {
        transform: scale(1.1);
      }
    }
    // @media screen and (max-width: 430px){
    //   display: none;
    // }
  }

  .home-main {
    position: relative;
    display: grid;
    align-items: center;
    padding-top: 1rem;
    justify-items: center;
    height: 95%;

    .main-content {
      width: 80%;
      user-select: none;

      h1 {
        font-size: 3.7rem;
        position: relative;
        font-family: $font-2;

        @media screen and (max-width: 450px){
          font-size: 1.5rem;

        }
        @media screen and (max-width: 650px){
          font-size: 1.8rem;
        }
        @media screen and (min-width: 1200px){
          font-size: 5rem;
        }

        &:before {
          content: '';
          position: absolute;
          height: 225px;
          width: 225px;
          border-radius: 50%;
          border: 1px $color-2 solid;
          top: -40px;
          left: -180px;
          z-index: -1;
          animation: h1Anim 1.5s forwards ease;
          transform: scale(0);

          @media screen and (max-width: 850px){
            height: 110px;
            width: 110px;
            left: -40px;
            top: -15px;
          }

          @keyframes h1Anim {
            to {
              transform: scale(1);
            }
          }
        }
      }
      h2 {
        font-size: 4.2rem;
        font-weight: 100;

        @media screen and (max-width: 650px){
          font-size: 2rem;
        }
        @media screen and (min-width: 1200px){
          font-size: 5rem;
        }
      }
    }
  }
}
.header-img {
      // margin: 20px 50px;
      width: 75%;
      height: 20rem;
    
 
    @media screen and (max-width: 930px ) {   
        display: flex;
        flex-direction: column;
        padding: 1rem;
       
    }
    @media screen and (max-width: 700px ) {
      .header-img {
        width: 75%;
        height: 15rem;
      }
 
    @media screen and (max-width: 550px ) { 
      .header-img {
           margin-left: 0.2rem;
           max-width: 270px;
           min-width: 270px;
           height: 380px;
       }   
       .home .home-main .main-content h1 {
        font-size: 0.8rem;
    }
      } 
    }
  }
